.information-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 75vh;
  padding: 20px;
}
.banner-text-info {
  width: 90%;
  max-width: 800px;
  color: white;
  text-align: justify;
  padding: 20px;
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.6);
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
}

.skills-chart {
  width: 90%;
  max-width: 600px;
  background: rgba(255, 255, 255, 0.1);
  padding: 15px;
  border-radius: 10px;
  margin-top: 20px;
}

.skills-chart h2 {
  color: #fff;
  text-align: center;
  margin-bottom: 10px;
}

.skills-list {
  list-style-type: none;
  padding: 0;
}

.skills-list li {
  font-weight: bold;
  margin-bottom: 5px;
}

/* Responsive */
@media (max-width: 768px) {
  .banner-text-info {
    width: 100%;
    padding: 15px;
    font-size: 14px;
  }

  .skills-chart {
    width: 100%;
    max-width: none;
  }
}
