.theme-toggle-button {
  padding: 10px;
  border-radius: 50%;
  background: linear-gradient(to right, #696b6d, #6e6d6d);
  color: white;
  font-size: 1.2rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
}

.theme-toggle-button:hover {
  background: linear-gradient(135deg, #0d0d0d, #8b0000);
}

.theme-toggle-button:active {
  transform: scale(0.95);
}

.theme-toggle-button svg {
  color: white;
  transition: color 0.3s ease-in-out;
}

[data-theme="dark"] .theme-toggle-button {
  background: linear-gradient(to right, #1a1a1a, #333);
}

[data-theme="dark"] .theme-toggle-button svg {
  color: yellow;
}
