.banner-spam {
  text-align: center;
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  background-color: var(--accent-color);
  color: white;
  padding: 15px 30px;
  border-radius: 8px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  z-index: 1000;
  animation: banner-spam-slideIn 0.5s ease-in-out;
  transition: opacity 0.3s ease-out;
  max-width: 90%;
  /* Limita el ancho en pantallas móviles */
}

.banner-spam-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.banner-spam-close-button {
  background: none;
  border: none;
  color: white;
  font-size: 18px;
  cursor: pointer;
}

@keyframes banner-spam-slideIn {
  from {
    transform: translateX(-50%) translateY(100px);
    opacity: 0;
  }

  to {
    transform: translateX(-50%) translateY(0);
    opacity: 1;
  }
}

/* Estilos para pantallas pequeñas */
@media (max-width: 600px) {
  .banner-spam {
    top: 52px;
    font-size: 14px;
    max-height: fit-content;
    width: max-content;
  }

  .banner-spam-close-button {
    font-size: 16px;
    /* Ajusta el tamaño de la "X" */
  }

  .banner-spam-content p {
    margin: 0;
    flex: 1;
    text-align: center;
  }
}