/* Contenedor principal del banner */
.banner {
    display: flex;
    height: 100vh;
  }

  .banner-text {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items: center;
    padding: 2rem;
    background: url('/public/imagenes/backg-banner-1.png') no-repeat center center;
    background-position: center;
  }

  .banner-text h1 {
    font-size: 3rem;
    margin-bottom: 1rem;
  }
  
  .banner-text h2 {
    color: var( --accent-color);
    font-weight: bold;
    font-size: 2rem;
    margin: 0;
  }

  .banner-image {
    flex: 1; 
    
    background-size: cover; 
    background-position: center;
    background-color: transparent;
  }

  @media(max-width: 768px){
    .banner{
      display: flex;
      flex-direction: column;
    }
    .banner-text h2 {
      font-size: 1.5rem;
    }
    .banner-text{

      background: url('/public/imagenes/backg-banner-1.png');
      background-size: cover;
    }
    .text-logo{
      margin-left: 20%;
    }
  }
  
  