.navbar {
  background: var(--primary-color);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  /*padding: 10px 15px;*/
  position: relative;
  top: 0;
  width: 100%;
  z-index: 1000;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* ====== LOGO EN EL CENTRO ====== */
.nav-logo-title {
  margin-right: 20%;
  flex: 1;
  text-align: center;
}

.text-logo {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto;
  font-size: 20px;
  text-transform: uppercase;
  color: var(--text-color-dark);
  text-decoration: none;
}

/* ====== MENÚ DE ESCRITORIO ====== */
.nav-links {
  list-style: none;
  display: flex;
  gap: 15px;
}

.navbar-item a {
  color: var(--text-color-dark);
  text-decoration: none;
  padding: 10px;
}

/* ====== MENÚ HAMBURGUESA ====== */
.menu-toggle {
  background: none;
  border: none;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  gap: 5px;
  position: absolute;
  left: 15px;
  z-index: 1100;
}

.menu-toggle .bar {
  width: 30px;
  height: 3px;
  background: var(--text-color-dark);
  transition: transform 0.3s, opacity 0.3s;
}

/* Animación del icono de hamburguesa */
.menu-toggle.open .bar:nth-child(1) {
  transform: translateY(8px) rotate(45deg);
}

.menu-toggle.open .bar:nth-child(2) {
  opacity: 0;
}

.menu-toggle.open .bar:nth-child(3) {
  transform: translateY(-8px) rotate(-45deg);
}

/* ====== MENÚ DESPLEGABLE SOLO PARA MÓVIL ====== */
@media (max-width: 768px) {
  .navbar {
    padding: 0;
  }
  h1{
    text-align: end;
  }
  .nav-links {
    display: none;
  }

  .mobile-menu {
    position: absolute;
    top: 50px;
    left: 0;
    width: 100%;
    background: var(--primary-color);
    display: flex;
    flex-direction: column;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }

  .mobile-menu.active {
    opacity: 1;
    visibility: visible;
  }

  .mobile-nav-item {
    padding: 15px;
    color: #ffffff;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  }

  .mobile-nav-item:hover {
    background: rgba(255, 255, 255, 0.1);
  }
}